import React from 'react'
import Home from '../router/Home';
import Doc from '../router/Doc';
import Tuto from '../router/Tuto';
import Blog from '../router/Blog';
import Comm from '../router/Comm';
import Login from './Login';
const Core=()=>{
    return(
        <h1>
            Core
        </h1>
    )
}
const Guide=()=>{
    return(
        <h1>
            Guide
        </h1>
    )
}
const Api=()=>{
    return(
        <h1>
            Api
        </h1>
    )
}
const Hooks=()=>{
    return(
        <h1>
            Hooks
        </h1>
    )
}
const routes = [
    {
        path: '/',
        exact: true,
        component: Home
    },
    {
        path: '/doc',
        component: Doc,
        pri: true,
        routes: [
            {
                path: '/doc/core',
                component: Core,
            },
            {
                path: '/doc/guide',
                component: Guide,
            },
            {
                path: '/doc/api',
                component: Api,
            },
            {
                path: '/doc/hooks',
                component: Hooks,
            },
        ]
    },
    {
        path: '/tuto',
        component: Tuto,
        pri: true
    },
    {
        path: '/blog',
        component: Blog
    },
    {
        path: '/comm',
        component: Comm
    },
    {
        path: '/login',
        component: Login
    },
]
export default routes;